import React from 'react';

interface FiveRingsLogoProps {
  style?: React.CSSProperties;
}

const FiveRingsLogo: React.FC<FiveRingsLogoProps> = ({ style }) => {
  // 五行色及顺序
  const beads = [
    { color: '#4caf50', label: '木' }, // green
    { color: '#e53935', label: '火' }, // red
    { color: '#fbc02d', label: '土' }, // yellow
    { color: '#e0e0e0', label: '金' }, // white
    { color: '#222', label: '水' },   // black
  ];
  const N = beads.length;
  const R = 24; // 珠子半径放大一倍
  const cx = 56, cy = 56, bigR = 36; // 圆心和大圆半径放大一倍
  // 计算五个珠子的圆心坐标
  const positions = beads.map((_, i) => {
    const angle = (2 * Math.PI / N) * i - Math.PI / 2;
    return {
      x: cx + bigR * Math.cos(angle),
      y: cy + bigR * Math.sin(angle),
    };
  });
  // 叠加顺序：让黄色（2）在红色（1）后面渲染
  const drawOrder = [3, 4, 0, 1, 2];
  return (
    <svg 
      width="96" 
      height="96" 
      viewBox="0 0 112 112" 
      style={{
        ...style,
        pointerEvents: 'none',
        position: 'relative',
        zIndex: 1
      }}
    >
      {drawOrder.map(i => (
        <circle
          key={i}
          cx={positions[i].x}
          cy={positions[i].y}
          r={R}
          fill={beads[i].color}
          stroke="#fff"
          strokeWidth={6}
          style={{ filter: 'drop-shadow(0 2px 4px #bbb)' }}
        />
      ))}
    </svg>
  );
};

export default FiveRingsLogo; 